import React, { useState, useEffect, useContext } from "react";
import Papa from "papaparse";
import { Link } from "react-router-dom";
import {
  Box,
  Card,
  CardContent,
  CardMedia,
  CircularProgress,
  Container,
  Typography,
} from "@mui/material";
import { CacheContext } from "../../contexts/CacheContext"; // CacheContext import
import * as styles from "./Weapons.styles";

//    ID . GID 0 .
const BASE_URL_ID = "1IZra9ZZRwBBgT4ai1W0fCATeFFsztHnF0k03DmLr1tI";
const GID = "0";
const SHEET_URL = `https://docs.google.com/spreadsheets/d/${BASE_URL_ID}/export?format=csv&gid=${GID}`;

// src/assets/images/weapons    .
//        .
const images = require.context(
  "../../assets/images/weapons",
  false,
  /\.(png|jpe?g|svg|webp)$/
);

const weaponImages = images.keys().reduce((acc, item) => {
  const key = item.replace("./", "");
  acc[key] = images(item);
  return acc;
}, {});

export default function Weapons() {
  const { cache, setCacheValue } = useContext(CacheContext);
  const [weapons, setWeapons] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      //  'weapons'   
      if (cache.weapons) {
        //  (  )    .
        const seen = new Set();
        const uniqueWeaponsForDisplay = cache.weapons.filter((row) => {
          const name = row[""];
          if (name && !seen.has(name)) {
            seen.add(name);
            return true;
          }
          return false;
        });

        setWeapons(uniqueWeaponsForDisplay);
        setLoading(false);
        return; //   fetch   
      }

      setLoading(true);
      setError(null);
      try {
        const response = await fetch(SHEET_URL);
        if (!response.ok) {
          throw new Error("   .");
        }
        const csvText = await response.text();

        Papa.parse(csvText, {
          header: true,
          skipEmptyLines: true,
          complete: (results) => {
            const allWeaponsData = results.data.filter(
              (row) => row[""] && row[""].trim() !== ""
            );

            const seen = new Set();
            const uniqueWeaponsForDisplay = allWeaponsData.filter((row) => {
              const name = row[""];
              if (!seen.has(name)) {
                seen.add(name);
                return true;
              }
              return false;
            });
            setWeapons(uniqueWeaponsForDisplay);
            setCacheValue("weapons", allWeaponsData); //     
            setLoading(false);
          },
          error: (err) => {
            throw new Error(err.message);
          },
        });
      } catch (e) {
        setError(e.message);
        setLoading(false);
      }
    };

    fetchData();
  }, [cache.weapons, setCacheValue]);

  const getWeaponNameStyle = (name) => {
    //       .
    if (name && name.length > 8) {
      return { ...styles.weaponName, fontSize: "0.875rem" };
    }
    return styles.weaponName;
  };

  if (loading) {
    return (
      <Box sx={{ display: "flex", justifyContent: "center", p: 3 }}>
        <CircularProgress />
        <Typography sx={{ ml: 2 }}>   ...</Typography>
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ display: "flex", justifyContent: "center", p: 3 }}>
        <Typography color="error">: {error}</Typography>
      </Box>
    );
  }

  return (
    <Container sx={styles.container}>
      <Typography variant="h5" gutterBottom>
          
      </Typography>
      <Box sx={styles.gridContainer}>
        {weapons.map((weapon, index) => (
          <Link
            to={`/weapons/${weapon[""].replace(/ /g, "_")}`}
            key={index}
            style={{ textDecoration: "none" }}
          >
            <Card sx={styles.card(weapon[""])}>
              <CardMedia
                component="img"
                sx={styles.cardImage}
                image={weaponImages[`${weapon[" "]}.webp`]}
                alt={weapon[""]}
              />
              <CardContent sx={styles.cardContent}>
                <Typography
                  variant="subtitle1"
                  sx={getWeaponNameStyle(weapon[""])}
                >
                  {weapon[""]}
                </Typography>
              </CardContent>
            </Card>
          </Link>
        ))}
      </Box>
    </Container>
  );
}
