import React, {
  useState,
  useEffect,
  useContext,
  useMemo,
  useCallback,
} from "react";
import Papa from "papaparse";
import {
  Box,
  Typography,
  CircularProgress,
  Paper,
  Button,
  Modal,
  Card,
  CardActionArea,
  CardMedia,
  CardContent,
  Menu,
  MenuItem,
  Switch,
  FormControlLabel,
  Snackbar,
} from "@mui/material";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { CacheContext } from "../../../contexts/CacheContext";
import * as styles from "./SpecialWeapon.styles";

// Weapons.js  Google Sheet  .
const BASE_URL_ID = "1IZra9ZZRwBBgT4ai1W0fCATeFFsztHnF0k03DmLr1tI";
const WEAPONS_GID = "0";
const GUARANTEED_ENH_GID = "2088796296"; //     GID
const PROB_ENH_GID = "665507476"; //     GID

const WEAPONS_SHEET_URL = `https://docs.google.com/spreadsheets/d/${BASE_URL_ID}/export?format=csv&gid=${WEAPONS_GID}`;
const GUARANTEED_ENH_URL = `https://docs.google.com/spreadsheets/d/${BASE_URL_ID}/export?format=csv&gid=${GUARANTEED_ENH_GID}`;
const PROB_ENH_URL = `https://docs.google.com/spreadsheets/d/${BASE_URL_ID}/export?format=csv&gid=${PROB_ENH_GID}`;

//   
const images = require.context(
  "../../../assets/images/weapons",
  false,
  /\.(png|jpe?g|svg|webp)$/
);

const weaponImages = images.keys().reduce((acc, item) => {
  const key = item.replace("./", "");
  acc[key] = images(item);
  return acc;
}, {});

const SIM_STATE_CACHE_KEY = "specialWeaponEnhSimState";

export default function SpecialWeapon() {
  const { cache, setCacheValue } = useContext(CacheContext);
  const [weapons, setWeapons] = useState([]);
  const [guaranteedEnhCosts, setGuaranteedEnhCosts] = useState([]);
  const [probEnhCosts, setProbEnhCosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const initialTotalCosts = {
    gold: 0,
    formlessShards: 0,
    refinedStones: 0,
    rawOres: 0,
    purplePowder: 0,
    yellowPowder: 0,
    consumedWeapons: {},
  };

  // useState  (lazy initial state)  sessionStorage totalCosts   .
  const [selectedWeaponName, setSelectedWeaponName] = useState(() => {
    try {
      const savedStateJSON = sessionStorage.getItem(SIM_STATE_CACHE_KEY);
      if (savedStateJSON) {
        const savedState = JSON.parse(savedStateJSON);
        // uniqueWeaponNames   ,    .
        //  useEffect     .
        return savedState.selectedWeaponName || "";
      }
    } catch (error) {
      console.error(
        "Failed to parse selectedWeaponName from sessionStorage",
        error
      );
    }
    return "";
  });

  const [currentLevel, setCurrentLevel] = useState(() => {
    try {
      const savedStateJSON = sessionStorage.getItem(SIM_STATE_CACHE_KEY);
      if (savedStateJSON) {
        const savedState = JSON.parse(savedStateJSON);
        return savedState.currentLevel || 0;
      }
    } catch (error) {
      console.error("Failed to parse currentLevel from sessionStorage", error);
    }
    return 0;
  });

  const [totalCosts, setTotalCosts] = useState(() => {
    try {
      const savedStateJSON = sessionStorage.getItem(SIM_STATE_CACHE_KEY);
      if (savedStateJSON) {
        const savedState = JSON.parse(savedStateJSON);
        if (savedState.totalCosts) {
          //   consumedWeapons  
          if (
            typeof savedState.totalCosts.consumedWeapons !== "object" ||
            savedState.totalCosts.consumedWeapons === null
          ) {
            savedState.totalCosts.consumedWeapons = {};
          }
          return savedState.totalCosts;
        }
      }
    } catch (error) {
      console.error("Failed to parse totalCosts from sessionStorage", error);
    }
    //        
    return initialTotalCosts;
  });

  const [skipMenuAnchorEl, setSkipMenuAnchorEl] = useState(null);
  const [isFallPreventionOn, setIsFallPreventionOn] = useState(false);
  const [isResetPreventionOn, setIsResetPreventionOn] = useState(false);
  const [snackbarInfo, setSnackbarInfo] = useState({
    open: false,
    message: "",
  });

  const getBaseWeaponData = useCallback(
    (name) => {
      return (
        weapons.find(
          (w) => w[""] === name && parseInt(w[" "], 10) === 0
        ) || weapons.find((w) => w[""] === name)
      );
    },
    [weapons]
  );

  const uniqueWeaponNames = useMemo(() => {
    //     
    const excludedNames = new Set([
      //     . : " "
      ", ",
      " ",
      "",
      " ",
      "",
      "",
      " ",
      " ",
      " ",
    ]);

    //     
    const excludedGrades = new Set([
      //    . : ""
      "",
      "",
      "",
    ]);

    //      .
    const nameCounts = weapons.reduce((acc, w) => {
      const name = w[""];
      if (name) {
        acc[name] = (acc[name] || 0) + 1;
      }
      return acc;
    }, {});

    //      .
    const allUniqueNames = Array.from(new Set(weapons.map((w) => w[""])));

    //  :
    // 1.   2  ( )
    // 2.     
    // 3.     
    return allUniqueNames.filter((name) => {
      if (!name || nameCounts[name] <= 1 || excludedNames.has(name)) {
        return false;
      }
      const weapon = getBaseWeaponData(name);
      return weapon && !excludedGrades.has(weapon[""]);
    });
  }, [weapons, getBaseWeaponData]);

  //   ,      
  useEffect(() => {
    if (
      uniqueWeaponNames.length > 0 &&
      selectedWeaponName &&
      !uniqueWeaponNames.includes(selectedWeaponName)
    ) {
      setSelectedWeaponName("");
    }
  }, [uniqueWeaponNames, selectedWeaponName]); // uniqueWeaponNames   

  //    sessionStorage 
  useEffect(() => {
    const stateToSave = { selectedWeaponName, currentLevel, totalCosts };
    sessionStorage.setItem(SIM_STATE_CACHE_KEY, JSON.stringify(stateToSave));
  }, [selectedWeaponName, currentLevel, totalCosts]);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setError(null);

      const fetchAndParse = async (url, cacheKey) => {
        if (cache[cacheKey]) {
          return cache[cacheKey];
        }
        const response = await fetch(url);
        if (!response.ok) {
          throw new Error(`'${cacheKey}'    .`);
        }
        const csvText = await response.text();
        return new Promise((resolve, reject) => {
          Papa.parse(csvText, {
            header: true,
            skipEmptyLines: true,
            complete: (results) => {
              const data = results.data.filter((row) =>
                Object.values(row).some((val) => val && val.trim() !== "")
              );
              setCacheValue(cacheKey, data);
              resolve(data);
            },
            error: (err) => reject(new Error(err.message)),
          });
        });
      };

      try {
        const [weaponsData, guaranteedData, probData] = await Promise.all([
          fetchAndParse(WEAPONS_SHEET_URL, "weapons"),
          fetchAndParse(GUARANTEED_ENH_URL, "guaranteedEnhCosts"),
          fetchAndParse(PROB_ENH_URL, "probEnhCosts"),
        ]);

        setWeapons(weaponsData);
        setGuaranteedEnhCosts(guaranteedData);
        setProbEnhCosts(probData);
      } catch (e) {
        setError(e.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [cache, setCacheValue]);

  const selectedWeaponData = useMemo(() => {
    if (!selectedWeaponName) return null;
    // 0   ,     
    return (
      weapons.find(
        (w) =>
          w[""] === selectedWeaponName && parseInt(w[" "], 10) === 0
      ) || weapons.find((w) => w[""] === selectedWeaponName)
    );
  }, [weapons, selectedWeaponName]);

  const availableEnhancementLevels = useMemo(() => {
    if (!selectedWeaponName) return [];
    return weapons
      .filter((w) => w[""] === selectedWeaponName)
      .map((w) => parseInt(w[" "], 10))
      .filter((level) => !isNaN(level))
      .sort((a, b) => a - b);
  }, [weapons, selectedWeaponName]);

  const handleSkipMenuOpen = (event) => {
    setSkipMenuAnchorEl(event.currentTarget);
  };
  const handleSkipMenuClose = () => setSkipMenuAnchorEl(null);

  const handleOpenModal = () => setIsModalOpen(true);
  const handleCloseModal = () => setIsModalOpen(false);

  const guaranteedEnhCost = useMemo(() => {
    if (!selectedWeaponData) return null;

    const nextLevel = currentLevel + 1;
    return guaranteedEnhCosts.find(
      (cost) =>
        cost[""] === selectedWeaponData[""] &&
        parseInt(cost[" "], 10) === nextLevel
    );
  }, [selectedWeaponData, currentLevel, guaranteedEnhCosts]);

  const probEnhCost = useMemo(() => {
    if (!selectedWeaponData) return null;

    const nextLevel = currentLevel + 1;
    return probEnhCosts.find(
      (cost) =>
        cost[""] === selectedWeaponData[""] &&
        parseInt(cost[" "], 10) === nextLevel
    );
  }, [selectedWeaponData, currentLevel, probEnhCosts]);

  const displayProbabilities = useMemo(() => {
    if (!probEnhCost) return null;

    let success = parseFloat(probEnhCost[" "]) || 0;
    let maintain = parseFloat(probEnhCost[" "]) || 0;
    let fall = parseFloat(probEnhCost[" "]) || 0;
    let reset = parseFloat(probEnhCost[" "]) || 0;

    if (isFallPreventionOn) {
      const reducedSuccess = success / 2;
      maintain += reducedSuccess; //      
      maintain += fall; //      
      success = reducedSuccess; //    
      fall = 0; //   0 
    }

    return {
      success,
      maintain,
      fall,
      reset,
    };
  }, [probEnhCost, isFallPreventionOn]);

  const handleWeaponSelect = (name) => {
    setSelectedWeaponName(name);
    setCurrentLevel(0); //      
    handleCloseModal();
  };

  //     
  useEffect(() => {
    setIsFallPreventionOn(false);
    setIsResetPreventionOn(false);
  }, [selectedWeaponName]);

  //    ,     
  useEffect(() => {
    const nextProbEnhCost = probEnhCosts.find(
      (cost) =>
        cost[""] === selectedWeaponData?.[""] &&
        parseInt(cost[" "], 10) === currentLevel + 1
    );

    if (!(parseFloat(nextProbEnhCost?.[" "]) > 0))
      setIsFallPreventionOn(false);
    if (!(parseFloat(nextProbEnhCost?.[" "]) > 0))
      setIsResetPreventionOn(false);
  }, [currentLevel, probEnhCosts, selectedWeaponData]);

  const handleGuaranteedEnhance = () => {
    if (!guaranteedEnhCost) return;

    const goldCost = parseInt(guaranteedEnhCost[""], 10) || 0;
    const shardCost = parseInt(guaranteedEnhCost[" "], 10) || 0;

    setTotalCosts((prev) => ({
      ...prev,
      gold: prev.gold + goldCost,
      formlessShards: prev.formlessShards + shardCost,
      //         
    }));

    setCurrentLevel((prev) => prev + 1);
  };

  const handleProbEnhance = () => {
    if (!probEnhCost) return;

    let goldCost = parseInt(probEnhCost[""], 10) || 0;
    const refinedStoneCost = parseInt(probEnhCost[" "], 10) || 0;
    const rawOreCost = parseInt(probEnhCost["  "], 10) || 0;

    let successRate = parseFloat(probEnhCost[" "]) || 0;
    let fallRate = parseFloat(probEnhCost[" "]) || 0; // ' '  
    const resetRate = parseFloat(probEnhCost[" "]) || 0;
    // ' '  ' ' .
    // let failAndMaintainRate = parseFloat(probEnhCost[" "]) || 0;

    if (isFallPreventionOn) {
      goldCost += parseInt(probEnhCost["  "], 10) || 0;

      const reducedSuccessRate = successRate / 2;
      // failAndMaintainRate += reducedSuccessRate; //    ()  
      // failAndMaintainRate += fallRate; //    ()  
      successRate = reducedSuccessRate; //    
      fallRate = 0; //   0 
    }
    if (isResetPreventionOn) {
      goldCost += parseInt(probEnhCost["  "], 10) || 0;
    }

    //         
    const getPowderCostByLevel = (level) => {
      if (level >= 7 && level <= 9) return 1;
      if (level >= 10 && level <= 12) return 2;
      if (level >= 13 && level <= 15) return 3;
      return 0;
    };

    const rand = Math.random();
    let resultMessage = "";
    let additionalCosts = {
      gold: goldCost,
      refinedStones: refinedStoneCost,
      rawOres: rawOreCost,
      purplePowder: 0,
      yellowPowder: 0,
    };

    if (rand < successRate) {
      // 
      setCurrentLevel((prev) => prev + 1);
      resultMessage = ` ! (+${currentLevel + 1})`;
    } else if (rand < successRate + fallRate) {
      //      
      //  ()
      if (!isFallPreventionOn) {
        setCurrentLevel((prev) => Math.max(0, prev - 1));
        resultMessage = ` ... (${
          currentLevel > 0 ? `+${currentLevel - 1} ` : " "
        })`;
      } else {
        resultMessage = " ! ( )";
      }
    } else if (rand < successRate + fallRate + resetRate) {
      //     
      // 
      if (!isResetPreventionOn) {
        setCurrentLevel(0);
        resultMessage = " ... (+0)";
      } else {
        //        
        const weaponGrade = selectedWeaponData[""];
        let additionalResultMessage = "";

        if (weaponGrade === "") {
          //   setTotalCosts  
          additionalResultMessage = " (  1 )";
        } else if (weaponGrade === "" || weaponGrade === "") {
          const powderCost = getPowderCostByLevel(currentLevel); //   
          if (powderCost > 0) {
            if (weaponGrade === "") {
              additionalCosts.purplePowder += powderCost;
              additionalResultMessage = ` (  ${powderCost} )`;
            } else {
              additionalCosts.yellowPowder += powderCost;
              additionalResultMessage = ` (  ${powderCost} )`;
            }
          }
        }

        resultMessage = ` ! ( )${additionalResultMessage}`;
      }
    } else {
      //    
      // 
      resultMessage = " ! ( )";
    }

    setTotalCosts((prev) => ({
      ...prev,
      gold: prev.gold + additionalCosts.gold,
      refinedStones: prev.refinedStones + additionalCosts.refinedStones,
      rawOres: prev.rawOres + additionalCosts.rawOres,
      purplePowder: prev.purplePowder + additionalCosts.purplePowder,
      yellowPowder: prev.yellowPowder + additionalCosts.yellowPowder,
      consumedWeapons:
        resultMessage.includes(" ") &&
        selectedWeaponData[""] === ""
          ? {
              ...(prev.consumedWeapons || {}),
              [selectedWeaponName]:
                ((prev.consumedWeapons &&
                  prev.consumedWeapons[selectedWeaponName]) ||
                  0) + 1,
            }
          : prev.consumedWeapons,
    }));

    setSnackbarInfo({ open: true, message: resultMessage });
  };

  const handleSkipToLevel = (targetLevel) => {
    //       .
    setCurrentLevel(targetLevel);

    handleSkipMenuClose();
  };

  const handleResetCosts = () => {
    setTotalCosts(initialTotalCosts);
    setCurrentLevel(0);
    setSnackbarInfo({ open: true, message: "  ." });
  };

  const handleSnackbarClose = (event, reason) => {
    if (reason === "clickaway") return;
    setSnackbarInfo({ ...snackbarInfo, open: false });
  };

  if (loading) return <CircularProgress />;
  if (error) return <Typography color="error">: {error}</Typography>;

  return (
    <Box sx={{ mb: 2 }}>
      <Button variant="contained" onClick={handleOpenModal} fullWidth>
        {selectedWeaponName || " "}
      </Button>

      <Modal open={isModalOpen} onClose={handleCloseModal}>
        <Box sx={styles.modalStyle}>
          <Typography variant="h6" component="h2" sx={{ mb: 2 }}>
             
          </Typography>
          <Box sx={styles.modalGrid}>
            {uniqueWeaponNames.map((name) => {
              const weapon = getBaseWeaponData(name);
              if (!weapon) return null;
              return (
                <Card key={name} sx={styles.modalWeaponCard(weapon[""])}>
                  <CardActionArea onClick={() => handleWeaponSelect(name)}>
                    <CardMedia
                      component="img"
                      sx={styles.modalWeaponImage}
                      image={weaponImages[`${weapon[" "]}.webp`]}
                      alt={name}
                    />
                    <CardContent sx={{ p: 0 }}>
                      <Typography sx={styles.modalWeaponName}>
                        {name}
                      </Typography>
                    </CardContent>
                  </CardActionArea>
                </Card>
              );
            })}
          </Box>
        </Box>
      </Modal>

      {selectedWeaponData && (
        <Paper elevation={3} sx={{ ...styles.selectedWeaponPaper, mt: 2 }}>
          <Box
            component="img"
            src={weaponImages[`${selectedWeaponData[" "]}.webp`]}
            alt={selectedWeaponData[""]}
            sx={styles.weaponImage}
          />
          <Box
            sx={{
              flex: 1,
              minWidth: 0,
              display: "flex",
              flexDirection: "column",
            }}
          >
            <Typography variant="h6" sx={styles.weaponName} noWrap>
              {selectedWeaponData[""]}
            </Typography>
            <Typography
              variant="caption"
              color="text.secondary"
              sx={{ mt: 0.5 }}
            >
              : {selectedWeaponData[""]}
            </Typography>
            <Box sx={{ display: "flex", alignItems: "center", mt: "auto" }}>
              <Typography variant="h6">+{currentLevel} </Typography>
              <Button
                variant="outlined"
                size="small"
                onClick={handleSkipMenuOpen}
                endIcon={<ArrowForwardIosIcon />}
                sx={{ flexShrink: 0, ml: "auto" }}
              >
                
              </Button>
              <Menu
                anchorEl={skipMenuAnchorEl}
                open={Boolean(skipMenuAnchorEl)}
                onClose={handleSkipMenuClose}
                anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
                transformOrigin={{ vertical: "top", horizontal: "right" }}
              >
                {availableEnhancementLevels.map((level) => (
                  <MenuItem
                    key={level}
                    onClick={() => handleSkipToLevel(level)}
                  >
                    +{level} 
                  </MenuItem>
                ))}
              </Menu>
            </Box>
          </Box>
        </Paper>
      )}

      {selectedWeaponData && (
        <Paper elevation={3} sx={{ p: 2, mt: 2 }}>
          <Box sx={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 2 }}>
            {/*   */}
            <Box sx={{ textAlign: "center" }}>
              <Typography variant="subtitle1" gutterBottom>
                 
              </Typography>
              {guaranteedEnhCost ? (
                <>
                  <Typography variant="body2">
                    : {Number(guaranteedEnhCost[""]).toLocaleString()}
                  </Typography>
                  <Typography variant="body2">
                     :{" "}
                    {Number(guaranteedEnhCost[" "]).toLocaleString()}
                  </Typography>
                  <Button
                    variant="outlined"
                    size="small"
                    sx={{ mt: 1 }}
                    onClick={handleGuaranteedEnhance}
                  >
                    
                  </Button>
                </>
              ) : (
                <Typography variant="body2" color="text.secondary">
                   
                </Typography>
              )}
            </Box>

            {/*   */}
            <Box sx={{ textAlign: "center" }}>
              <Typography variant="subtitle1" gutterBottom>
                 
              </Typography>
              {probEnhCost ? (
                <>
                  <Typography variant="body2">
                    : {Number(probEnhCost[""]).toLocaleString()}
                  </Typography>
                  <Typography variant="body2">
                     :{" "}
                    {Number(probEnhCost[" "]).toLocaleString()}
                  </Typography>
                  <Typography variant="body2">
                      :{" "}
                    {Number(probEnhCost["  "]).toLocaleString()}
                  </Typography>
                  <Box
                    sx={{
                      mt: 1,
                      display: "flex",
                      justifyContent: "center",
                      gap: 1,
                      flexWrap: "wrap",
                    }}
                  >
                    {displayProbabilities?.success > 0 && (
                      <Typography variant="caption">
                        : {(displayProbabilities.success * 100).toFixed(1)}%
                      </Typography>
                    )}
                    {displayProbabilities?.maintain > 0 && (
                      <Typography variant="caption" color="text.secondary">
                        : {(displayProbabilities.maintain * 100).toFixed(1)}
                        %
                      </Typography>
                    )}
                    {displayProbabilities?.fall > 0 && (
                      <Typography variant="caption" color="error">
                        : {(displayProbabilities.fall * 100).toFixed(1)}%
                      </Typography>
                    )}
                    {displayProbabilities?.reset > 0 && (
                      <Typography variant="caption" color="error">
                        : {(displayProbabilities.reset * 100).toFixed(1)}%
                      </Typography>
                    )}
                  </Box>

                  <Button
                    variant="outlined"
                    size="small"
                    sx={{ mt: 1 }}
                    onClick={handleProbEnhance}
                  >
                    
                  </Button>
                  <Box
                    sx={{
                      display: "flex",
                      flexDirection: "column",
                      alignItems: "center",
                      mt: 1,
                      gap: 1,
                    }}
                  >
                    {parseFloat(probEnhCost[" "]) > 0 && (
                      <Box>
                        <FormControlLabel
                          control={
                            <Switch
                              checked={isFallPreventionOn}
                              onChange={(e) =>
                                setIsFallPreventionOn(e.target.checked)
                              }
                              size="small"
                            />
                          }
                          label=" "
                        />
                        <Typography variant="caption" display="block">
                          :{" "}
                          {Number(
                            probEnhCost["  "]
                          ).toLocaleString()}{" "}
                          
                        </Typography>
                      </Box>
                    )}
                    {parseFloat(probEnhCost[" "]) > 0 && (
                      <Box>
                        <FormControlLabel
                          control={
                            <Switch
                              checked={isResetPreventionOn}
                              onChange={(e) =>
                                setIsResetPreventionOn(e.target.checked)
                              }
                              size="small"
                            />
                          }
                          label=" "
                        />
                        <Typography variant="caption" display="block">
                          :{" "}
                          {Number(
                            probEnhCost["  "]
                          ).toLocaleString()}{" "}
                           +{" "}
                          {(() => {
                            const grade = selectedWeaponData[""];
                            const level = currentLevel; //   
                            if (grade === "") return "  1";
                            if (grade === "") {
                              const cost =
                                level >= 7 && level <= 9
                                  ? 1
                                  : level >= 10 && level <= 12
                                  ? 2
                                  : 3;
                              return `  ${cost}`;
                            }
                            if (grade === "") {
                              const cost =
                                level >= 7 && level <= 9
                                  ? 1
                                  : level >= 10 && level <= 12
                                  ? 2
                                  : 3;
                              return `  ${cost}`;
                            }
                          })()}
                        </Typography>
                      </Box>
                    )}
                  </Box>
                </>
              ) : (
                <Typography variant="body2" color="text.secondary">
                   
                </Typography>
              )}
            </Box>
          </Box>
        </Paper>
      )}

      {selectedWeaponData &&
        (totalCosts.gold > 0 ||
          totalCosts.formlessShards > 0 ||
          totalCosts.refinedStones > 0 ||
          totalCosts.rawOres > 0 ||
          totalCosts.purplePowder > 0 ||
          totalCosts.yellowPowder > 0 ||
          (totalCosts.consumedWeapons &&
            Object.keys(totalCosts.consumedWeapons).length > 0)) && (
          <Paper elevation={3} sx={{ p: 2, mt: 2, position: "relative" }}>
            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                mb: 1,
              }}
            >
              <Typography variant="h6">  </Typography>
              <Button
                variant="outlined"
                size="small"
                onClick={handleResetCosts}
              >
                
              </Button>
            </Box>
            <Typography>: {totalCosts.gold.toLocaleString()}</Typography>
            <Typography>
               : {totalCosts.formlessShards.toLocaleString()}
            </Typography>
            <Typography>
               : {totalCosts.refinedStones.toLocaleString()}
            </Typography>
            <Typography>
                : {totalCosts.rawOres.toLocaleString()}
            </Typography>
            {totalCosts.purplePowder > 0 && (
              <Typography>
                 : {totalCosts.purplePowder.toLocaleString()}
              </Typography>
            )}
            {totalCosts.yellowPowder > 0 && (
              <Typography>
                 : {totalCosts.yellowPowder.toLocaleString()}
              </Typography>
            )}
            {totalCosts.consumedWeapons &&
              Object.entries(totalCosts.consumedWeapons).map(
                ([name, count]) => (
                  <Typography key={name}>
                     {name}: {count.toLocaleString()}
                  </Typography>
                )
              )}
          </Paper>
        )}

      <Snackbar
        open={snackbarInfo.open}
        autoHideDuration={3000}
        onClose={handleSnackbarClose}
        message={snackbarInfo.message}
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
      />
    </Box>
  );
}
